import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
class LaporanDetailBarangExel extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
    render() {
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="btn btn-success btn-block"
          table="table-to-xls"
          filename="Laporan Barang Detail"
          sheet="Laporan Barang Detail"
          buttonText="Export Exel"
        />
        <table rowkey="kode_member" id="table-to-xls" style={{ display: "none" }}>
        <thead>
              <tr>
                <th colSpan="9" style={{textAlign : "center"}}> LAPORAN BARANG DETAIL </th>
              </tr>
              <tr>
                <th> Kode Baki </th>
                <th colSpan="9" style={{textAlign : "left"}}> {this.props.kode_baki} </th>
              </tr>
            <tr >
                <th  style={{ backgroundColor: "#E8E5E5", color: "#000"}}>NO</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>KODE BARANG</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>KODE BARCODE</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>KODE INTERN</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>KODE GUDANG</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>NAMA BARANG</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>BERAT</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>NAMA ATRIBUT</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>BERAT ATRIBUT</th>
                {/* <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>JUMLAH</th> */}
            </tr>
        </thead>
            
            <tbody>
            {this.props.data.map((item, index) => (
                    <tr key={index}>
                        <td style={{ textAlign : "center" }}> {index+1}</td>
                        <td> {item.kode_barang}</td>
                        <td> {item.kode_barcode}</td>
                        <td> {item.kode_intern}</td>
                        <td> {item.kode_gudang}</td>
                        <td> {item.nama_barang}</td>
                        <td> {item.berat}</td>
                        <td> {item.nama_atribut}</td>
                        <td> {item.berat_atribut}</td>
                        {/* <td style={{ border: "1px solid #000" }}> {item.stock_on_hand}</td> */}
                    </tr>
             ))}
            </tbody>
                    
            <tfoot>
                <tr>
                    <th style={{backgroundColor: "#E8E5E5", color: "#000",textAlign : "center" }} colSpan="6">  </th>    
                    <th style={{backgroundColor: "#E8E5E5", color: "#000"}}> {this.props.data.map(list => list.berat).reduce((a,b) => a+b,0)}   </th>    
                    <th style={{backgroundColor: "#E8E5E5", color: "#000"}}>  </th>    
                    <th style={{backgroundColor: "#E8E5E5", color: "#000",textAlign : "right"  }}> {this.props.data.map(list => list.berat_atribut).reduce((a,b) => a+b,0)}  </th>    
                    {/* <th style={{backgroundColor: "#E8E5E5", color: "#000"}}> {this.props.data.map(list => list.stock_on_hand).reduce((a,b) => a+b,0)}  </th>     */}
                </tr>
            </tfoot>
        </table>
      </>
    );
  }
}
export default LaporanDetailBarangExel;
