import jsPDF from "jspdf";
import "jspdf-autotable";
import { getToday, localStoragedecryp } from "../../../../../components/helpers/function";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const CetakLaporanDetailBarang = (
  data = "",
  kode_baki = "",
) => {
  // initialize jsPDF
  const doc = new jsPDF("l", "mm", [297, 210]);
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  // let footRows = [];
  let tableColumn = [];
  let finalY = 30;
  let total_berat = 0;
  let total_stock = 0;
  let total_berat_atribut = 0;


  doc.setFontSize(15);
  doc.text("LAPORAN BARANG DETAIL", 14, 15);
  doc.setFontSize(10);
  doc.setProperties({
    title: "LAPORAN BARANG DETAIL",
  });
  doc.text(`KODE BAKI : ${kode_baki}`, 14, 25);
  tableColumn = [
    [
      {
        content: `NO`,
      },
      {
        content: `KODE BARANNG`,
      },
      {
        content: `KODE BARCODE`,
      },
      {
        content: `KODE INTERN`,
      },
      {
        content: `KODE GUDANG`,
      },
      {
        content: `NAMA BARANG`,
      },
      {
        content: `BERAT`,
      },
      {
        content: `NAMA ATRIBUTE`,
      },
      {
        content: `BERAT ATRIBUTE`,
      },
      // {
      //   content: `JUMLAH`,
      // },
    ],
  ];

  data.forEach((item, index) => {
    // item.list_barang.forEach((barang, index) => {
    let rows = [
      // ++index,
      {
        content: `${++index}`,
        styles: {
          cellWidth : 10
        },
      },
      item.kode_barang,
      item.kode_barcode,
      item.kode_intern,
      item.kode_gudang,
      item.nama_barang,
      item.berat,
      item.nama_atribut,
      item.berat_atribut,
      // item.stock_on_hand,
     
    ];
    total_berat = total_berat + item.berat;
    total_stock = total_stock + item.stock_on_hand;
    total_berat_atribut = total_berat_atribut + item.berat_atribut;
    tableRows.push(rows);
  });
  
  // let footer = [
  //   ``,
  //   ``,
  //   ``,
  //   ``,
  //   ``,
  //   ``,
  //   `${total_berat.toFixed(1)}`,
  //   ``,
  //   `${total_berat_atribut.toFixed(1)}`,
  //   `${total_stock.toLocaleString('ID-id')}`,
  // ];
  let footer = [
    {
      content: `Total`,
      colSpan: 6,
      styles: {
        halign: 'center',
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: `${total_berat.toFixed(3)}`,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: ``,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: `${total_berat_atribut.toFixed(3)}`,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },

    // `${sub_qty}`,
  ];
  tableRows.push(footer);
  let printed = [
    {
      colSpan: 6,
      content: `Printed By ${JSON.parse(localStoragedecryp("userdata")).user_id} / ${getToday()}`,
      styles: {
        // lineWidth: 0.1,
        fontStyle: "italic",
        textColor: "#000",
        // fillColor: "#E8E5E5"
      },
    },
  ]
  tableRows.push(printed);
  doc.autoTable({
    head: tableColumn,
    body: tableRows,
    // foot: footRows,
    startY: finalY,
    theme: "plain",
    rowPageBreak: "avoid",
    // pageBreak: "avoid",
    margin: { top: 10 },
    bodyStyles: {
      // lineWidth: 0.02,
      // lineColor: "#000",
      fontSize: 8,
    },
    headStyles: {
      fontSize: 8,
      // lineWidth: 0.02,
      // lineColor: "#000",
      textColor: "#000",
      fillColor: "#E8E5E5",
    },
  });
  finalY = doc.autoTableEndPosY() + 3;
  tableRows = [];
  // footRows = [];
  // total_berat = 0;
  // const date = Date().split(" ");
  // we use a date string to generate our filename.
  // const dateStr = date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left
  // doc.text(`Total Berat                    :  ${total_berat.toFixed(1)}`, 14, finalY + 10);
  // doc.text(`Berat Atribut                  :  ${total_berat_atribut.toFixed(1)}`, 14, finalY + 16);
  // doc.text(`Stock                             :  ${total_stock.toLocaleString('ID-id')}`, 14, finalY + 22);
  // doc.text(`User                              :  ${JSON.parse(localStoragedecryp("userdata")).user_id}`, 14, finalY + 10);
  // doc.text(`Cetak                            : ${getToday()}`, 14, finalY + 16);
  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  // doc.autoPrint();
  // doc.save(`LAPORAN BARANG DETAIL.pdf`);
  var string = doc.output("datauristring");
  // var embed = "<embed width='100%' height='100%' src='" + string + "'/>";
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN BARANG DETAIL</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // x.document.write(embed);
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default CetakLaporanDetailBarang;
